body {
  background: #f7f9fa;
  font-family: "Segoe UI", Arial, sans-serif;
  margin: 0;
  padding: 0;
}

header {
  background-color: #c62828;
  color: #fff;
  padding: 16px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.08);
}

.sidebar {
  background-color: #222;
  color: #fff;
  width: 220px;
  min-height: 100vh;
  position: fixed;
  padding: 24px 0;
}

.sidebar ul {
  padding-left: 0;
  list-style: none;
}

.sidebar li {
  margin-bottom: 18px;
}

.sidebar button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1rem;
  width: 100%;
  text-align: left;
  padding: 8px 16px;
  border-radius: 6px;
}

.sidebar button.active,
.sidebar button:hover {
  background-color: #c62828;
}

.main {
  margin-left: 220px;
  padding: 32px 40px;
}

.card {
  background: #fff;
  box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 28px;
}

.btn {
  background-color: #c62828;
  color: #fff;
  border: none;
  padding: 10px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
}

.btn:disabled,
.btn.disabled {
  background-color: #bbb;
  color: #eee;
  cursor: not-allowed;
}

.status-green {
  color: #388e3c;
  font-weight: bold;
}

.status-yellow {
  color: #fbc02d;
  font-weight: bold;
}

.status-red {
  color: #c62828;
  font-weight: bold;
}

/* Add extra classes as needed for your design */