.sticky-header {
    position: sticky;
    top: 0;
    z-index: 100;
    background: #c62828;
    color: #fff;
}

.header-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-logo-title {
    display: flex;
    align-items: center;
    gap: 12px;
}

.status-red {
    color: #c62828;
    font-weight: bold;
}